import React from 'react'

export default function(props){
  return (
    <form className="signIn" onSubmit={props.onSubmit}> {/**/}
      <div className="row">
        <input type="text" value={props.formData.username} placeholder=""
          onChange={props.onChange.bind(null,'username')} />
      </div>
      <div className="row">
        <input type="password" value={props.formData.password} placeholder=""
          onChange={props.onChange.bind(null,'password')} />
      </div>
      <div className="row action">
        <button type="submit"></button>
        <p className="btnFgt" onClick={props.onForgotPassword}></p>
      </div>
    </form> 
  )
}


