import React from 'react'

export default function(props){
  return (
    <form className="signUp" onSubmit={props.onSubmit.bind(this)}> {/**/}
      <div className="row">
        <input type="text" value={props.formData.username} placeholder="user"
          onChange={props.onChange.bind(null,'username')} />
      </div>
      <div className="row">
        <input type="email" value={props.formData.email} placeholder=""
          onChange={props.onChange.bind(null,'email')} />
      </div>
      <div className="row">
        <input type="password" value={props.formData.password} placeholder=""
          onChange={props.onChange.bind(null,'password')} />
      </div>
      <div className="row action">
        <button type="submit"></button>
      </div>
    </form>    
  )
}


